.block {
  width: 100%;
  height: 50px;
  border-bottom: 1px solid #bbb;
  border-top: 1px solid #bbb;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
}
.text {
  width: 50%;
  margin: 0;
  font-size: 16px;
  font-family: Comfortaa;
  cursor: default;
}
.text_2 {
  margin: 0;
  font-size: 16px;
  font-family: Comfortaa;
  cursor: default;
}
.block_3 {
  padding-top: 20px;
  width: 100%;
  height: 400px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.btn {
  width: 220px;
  border: none;
  border: 1px solid rgba(0, 0, 0, 0);
  background-color: #fe724c;
  color: white;
  border-radius: 60px;
  padding: 18px 30px;
  cursor: pointer;
  font-family: Comfortaa;
}
.block_4 {
  width: 100%;
  height: 320px;
  border-bottom: 1px solid #fe724c;
  border-top: 1px solid #fe724c;
}
